//及时反馈
import HMmessages from "../../components/HM-messages/HMmessages.vue"
export default {
	name:"travels",
	components:{
		HMmessages
	},
	data() {
		return {
			uploadvideos: false,
			watchaddress: '',
			classData: '景点',  //分类
			titleData: '',   //标题内容
			contentData: '',   //描述
			imgs: [],   //上传的图片
			videos: '',  //上传的视频
			address: '', //选择的城市
			avatarUrl: '', // 用户头像
			nickName: ''
		};
	},

	methods: {
		// 上传图片
		uploadImg(){
			uni.chooseImage({
				count: 9, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res)=> {
					this.topimg.push(...res.tempFilePaths)

				}
			});
		},
		// 删除图片
		deleteImg(index){
			console.log(index)
			this.topimg.splice(index,1)
			console.log(this.topimg)
		},
		// 预览图片
		preImage(index){
			// 预览图片
			let imglist = this.topimg
			preview(index,imglist)
					.then((res)=>{
						console.log('预览成功')
					})
					.catch((err)=>{
						console.log('预览失败')
					})
		},
		// 提交
		submit(){
			if(this.titleData == ''){
				let message = '亲，记得写标题哦'
				this.alert(message)
			}else if(this.contentData == ''){
				let message = '亲，内容一定要填哦'
				this.alert(message)
			}else if(this.imgs.length < 3){
				let message = '上传的图片不少于三张'
				this.alert(message)
			}else{
				console.log('可以提交' +this.titleData)
				console.log('可以提交' +this.contentData)
				// 判断用户是否登录，登录再提交
				// this.userinfo()
			}
		},
		// 及时反馈
		alert(message){
			this.HMmessages.show(message,{iconColor:"#ffffff",background: "#ffd655"})
		}
	}
}
